html {
  --color-black: #0e111b;
  --color-white: #fff;
  --color-white-50: #ffffff80;
  --color-white-30: #ffffff4d;
  --color-white-20: #fff3;
  --color-white-10: #ffffff1a;
  --color-white-5: #ffffff0d;
  --color-primary: #fdc000;
  --color-hover: #f86a05;
  --color-area: #1c212e;
}

body {
  color: var(--color-white);
  background-color: #222;
  font-family: Roboto, sans-serif;
  line-height: 1.4;
  position: relative;
}

.container {
  margin: 0 auto;
  inset: 0;
}

.btn {
  color: var(--color-white);
  background-color: var(--color-area);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  height: 50px;
  padding: 0 20px;
  font-size: 14px;
  font-weight: 700;
  transition: all .3s;
}

.btn:hover {
  background-color: var(--color-hover);
}

.btn-primary {
  background-color: var(--color-primary);
  color: var(--color-black);
}

.btn-primary:hover {
  background-color: var(--color-hover);
  color: var(--color-white);
}

.the-loader {
  border: 4px solid var(--color-primary);
  border-top-color: #0000;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin: 30px auto;
  animation: 1s linear infinite loader;
}

.the-loader.hide {
  display: none;
}

@keyframes loader {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.skeleton {
  background-color: var(--color-area);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.skeleton:after {
  content: "";
  background-image: linear-gradient(270deg, #fff0, #ffffff1a, #fff0);
  width: 100%;
  height: 100%;
  animation: 2s infinite skeleton-loader;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

@keyframes skeleton-loader {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

.back-to-position {
  opacity: 0;
  transition: all 1s;
}

.back-to-position.to-right {
  transform: translateX(-150px);
}

.back-to-position.to-left {
  transform: translateX(150px);
}

.back-to-position.to-top {
  transform: translateY(-150px);
}

.back-to-position.to-bottom {
  transform: translateY(150px);
}

.show .back-to-position {
  opacity: 1;
  transform: translateX(0);
}

.show .back-to-position.delay-0 {
  transition-delay: 0s;
}

.show .back-to-position.delay-1 {
  transition-delay: .3s;
}

.show .back-to-position.delay-2 {
  transition-delay: .6s;
}

.show .back-to-position.delay-3 {
  transition-delay: .9s;
}

